.dashboard-wrapper {
  width: 65%;
}

.dashboard-control {
  width: 100%;
  text-align: center;
}

#map-btn,
#history-btn {
  padding: 1rem;
  font-size: larger;
  font-weight: bold;
}

.tab-btn {
  color: var(--clr-primary);
}

.tab-btn:hover {
  cursor: pointer;
}

.active-tab {
  text-decoration: underline;
  text-decoration-thickness: 3px;
}

.inactive-tab {
  color: var(--clr-gray);
}

.map-wrapper {
  display: flex;
  box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.33);
  -webkit-box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.33);
  -moz-box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.33);
}

.map-wrapper {
  height: 500px;
  width: 100%;
  display: flex;
}

.map-container {
  height: 500px;
  min-width: 600px;
}

.mapboxgl-ctrl {
  display: none;
}

/* Report list */

.map-sidebar {
  width: 100%;
  height: 500px;
}

.report-list .report-item .title {
  display: block;
  color: #00853e;
  font-weight: 700;
}

.report-list {
  overflow: scroll;
  height: 460px;
}

.report-item {
  border-bottom: 1px solid #eee;
  padding: 10px;
  text-decoration: none;
}

.report-list .report-item:last-child {
  border-bottom: none;
}

.report-list .report-item .title small {
  font-weight: 400;
}

.report-list .item.active .title,
.report-list .report-item .title:hover {
  color: var(--clr-secondary);
  cursor: pointer;
}

.report-list .report-item.active {
  background-color: #f8f8f8;
}

::-webkit-scrollbar {
  width: 3px;
  height: 3px;
  border-left: 0;
  background: rgba(0, 0, 0, 0.1);
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background: #00853e;
  border-radius: 0;
}

/* Marker tweaks */
.mapboxgl-popup-close-button {
  display: none;
}

.mapboxgl-popup-content {
  font-size: 14px;
  padding: 0;
  width: 180px;
}

.mapboxgl-popup-content h5 {
  background: var(--clr-primary);
  color: #fff;
  margin: 0;
  padding: 10px;
  font-size: 15px;
  border-radius: 3px 3px 0 0;
  font-weight: 700;
  margin-top: -15px;
}

.mapboxgl-popup-content h6 {
  margin: 0;
  padding: 10px;
  font-weight: 400;
}

.mapboxgl-popup-content div {
  padding: 10px;
}

.mapboxgl-popup-anchor-top > .mapboxgl-popup-content {
  margin-top: 15px;
}

.mapboxgl-popup-anchor-top > .mapboxgl-popup-tip {
  border-bottom-color: var(--clr-primary);
}

/* Analytics */

.analytics {
  text-align: center;
}

.analytics-control {
  display: flex;
  gap: 1rem;
  font-weight: bold;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
}

.chart {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* History */

.history {
  text-align: center;
}

.history-item {
  border-bottom: 1px solid var(--clr-gray);
  padding: 1rem;
}

.history-item-title {
  font-size: large;
  font-weight: bold;
  color: var(--clr-primary);
}

.history-item-list {
  box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.33);
  -webkit-box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.33);
  -moz-box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.33);
  border-radius: 25px;
  padding: 2rem;
  text-align: left;
  width: 400px;
  margin: auto;
  max-height: 500px;
  overflow: scroll;
}
