@import url("https://fonts.googleapis.com/css2?family=Rubik&display=swap");

:root {
  --clr-gray: #cad2d3;
  --clr-dark: #2a4447;
  --clr-primary: #5E8D88;
  --clr-secondary: #345D59;
}

body {
  margin: 0;
  font-family: "Rubik", "sans-serif";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

hr {
  margin: 0 !important;
}

.marker {
  border: none;
  cursor: pointer;
  height: 50px;
  width: 50px;
  background-image: url(marker.png);
}

.app-container {
  margin-top: 2rem;
}

.logo {
  opacity: 0.8;
}

.logo-wrapper {
  display: flex;
  margin: auto;
  justify-content: center;
  align-items: center;
  margin-right: 2rem;
  color: var(--clr-dark);
}
